import React, { useContext, useEffect, useMemo } from "react";
import { AccessibilityInfo, StyleSheet, View } from "react-native";
import {
  getCurrentEnemyAction,
  getEnemyActionProgress,
} from "../../backend/exploration/Exploration";
import { PlayerContext } from "../../backend/PlayerContext";
import { GameIcon } from "../utility/icons/GameIcon";
import { Markdown } from "../utility/Markdown";
import { ProgressBar } from "../utility/ProgressBar";
import { Text } from "../utility/Text";
import { UIContext } from "../utility/UIContext";

export function EnemyActionDisplay() {
  const playerContext = useContext(PlayerContext);
  const uiContext = useContext(UIContext);
  const currentEventOccurrenceId = uiContext.getUIState(
    "current_event_occurrence_id",
  );
  const currentEnemyAction = getCurrentEnemyAction(playerContext);
  const progress = getEnemyActionProgress(playerContext);
  useEffect(() => {
    if (currentEnemyAction?.name != null && currentEventOccurrenceId != null) {
      setTimeout(
        () =>
          AccessibilityInfo.announceForAccessibility(currentEnemyAction?.name),
        0,
      );
    }
  }, [currentEnemyAction, currentEventOccurrenceId]);
  return useMemo(() => {
    if (!currentEnemyAction) {
      return (
        <View>
          <Text h3>-</Text>
          <ProgressBar progress={0} useNativeDriver={true} />
        </View>
      );
    }
    return (
      <View style={styles.row}>
        <GameIcon icon="attack" style={styles.icon} />
        <View style={styles.progressBarContainer}>
          <Markdown
            accessibilityLiveRegion={
              currentEventOccurrenceId != null ? "polite" : "none"
            }
          >
            {currentEnemyAction.name}
          </Markdown>
          <ProgressBar
            progress={progress}
            useNativeDriver={true}
            width={null}
          />
        </View>
      </View>
    );
  }, [currentEnemyAction?.name, progress]);
}

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginRight: 4,
  },
  progressBarContainer: {
    flex: 1,
    minHeight: 32,
  },
});
