import React from "react";
import { StyleSheet, View } from "react-native";
import { EquipmentSetButtons } from "../inventory/EquipmentSetButtons";
import { AutocastLoadoutLoadContainer } from "../utility/AutocastLoadoutLoadContainer";
import CategoryContainer from "../utility/CategoryContainer";

export function LoadoutsContainer() {
  return (
    <View style={styles.mainContainer}>
      <View style={styles.actionsContainer}>
        <CategoryContainer
          title="Equipment Sets"
          containerStyle={styles.equipmentContainer}
        >
          <View style={styles.categoryContents}>
            <EquipmentSetButtons />
          </View>
        </CategoryContainer>
        <CategoryContainer
          title="Apprentice Loadouts"
          containerStyle={styles.autocastContainer}
        >
          <View style={styles.categoryContents}>
            <AutocastLoadoutLoadContainer />
          </View>
        </CategoryContainer>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  mainContainer: {
    height: "100%",
  },
  actionsContainer: {
    flex: 1.5,
    marginRight: 8,
  },
  equipmentContainer: {
    paddingTop: 0,
    marginBottom: 8,
  },
  autocastContainer: {
    paddingTop: 0,
  },
  categoryContents: {
    marginTop: 8,
  },
});
